@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono:wght@100&family=Domine&family=Nanum+Gothic&family=Noto+Serif+JP&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-family: var(--main-font);
}

:root {
  --white: #ffffff;
  --black: #000000;
  --orange: #ff8400;
  --light-transparent: rgba(255, 255, 255, 0.5);
  --dark-transparent: rgba(0, 0, 0, 0.5);
  --header-font: "Nanum Gothic", sans-serif;
  --header-font-secondary: "Noto Serif JP", serif;
  --main-font: "Chivo Mono", monospace;
  --main-font-secondary: "Domine", serif;
  --transition-5s: 0.5 ease-in-out;
}

*::-webkit-scrollbar {
  display: none;
}

body {
  background: black;
  padding: 15px;
}

.navbar {
  background: var(--white);
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  all: unset;
}

.transition {
  transition: 0.5s ease-in-out;
}

/* navbar styling */

.navbar {
  width: 100%;
  position: relative;
  top: 0;
  z-index: 975;
  border-radius: 5px 5px 0 0;
  /* border-bottom: 1px solid var(--orange); */
}

nav {
  width: 100%;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 15px 45px;
}

nav .navigation-logo {
  width: 2.5rem;
}

nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

nav ul li {
  padding: 5px 15px;
  margin: 5px 10px;
  font-size: 18px;
  font-family: var(--main-font-alt);
  letter-spacing: 1.2px;
  cursor: pointer;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease-in-out;
}

nav ul li:hover {
  border-bottom: 1px solid var(--orange);
  border-width: 1px;
}

nav .toggler {
  cursor: pointer;
  display: none;
}

nav .toggler img {
  width: 3rem;
}

nav .nav-active {
  transform: translateX(0);
}

/* main */

section {
  min-height: 100vh;
  width: 100%;
  background: var(--white);
}

#home {
  display: flex;
  align-items: center;
  justify-content: center;
}

#home header {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

#home header h1 {
  font-size: 88px;
  background-image: linear-gradient(to right, var(--orange), #DB6400, var(--black));
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  margin: 120px 0 0;
}

#home header h2 {
  font-size: 36px;
  margin: 20px 0 0;
}

#home header .container {
  margin: 120px 0 0;
  text-align: center;
}

#home header .container .text {
  font-size: 24px;
  margin: 120px 0 0;
  position: relative;
  color: var(--black);
  font-weight: bold;
}


/* Update the styles for .second-text element */
#home header .text.second-text {
  position: relative; /* Add relative position to establish a new stacking context for ::before pseudo-element */
  overflow: hidden;
  color: var(--orange);
}

#home header .text.second-text::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--white);
  border-left: 2px solid var(--black);
  animation: animate 4s steps(26) infinite;
}

@keyframes animate {
  40%, 60% {
    left: 100%;
  }

  100% {
    left: 0%;
  }
}



@media screen and (max-width: 900px) {
  .navbar {
    position: relative;
    top: 0;
  }

  nav .toggler {
    display: block;
  }

  nav .toggler .menu-btn {
    transform: rotate(180deg);
    transition: 1s ease-in-out;
  }

  nav .toggler .turned-btn {
    transform: rotate(-270deg);
  }

  nav ul {
    position: fixed;
    top: 10vh;
    right: 0;
    width: 100%;
    height: 90vh;
    flex-direction: column;
    transform: translateX(100%);
    background-color: var(--white);
    opacity: 0.9;
    transition: 0.5s ease-in-out;
  }
}

@media screen and (max-width: 768px) {
  .navbar {
    position: fixed;
    top: 0;
  }
}
